<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <b-table bordered responsive hover :items="userLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="role" slot-scope="row">
          <b-form-select v-model="row.item.role" size="sm" :options="roles" :disabled="!row.item.addImgFlag" />
        </template>
        <template slot="permission" slot-scope="row">
          <div class="ghTagCon">
            <div class="ghTag" v-for="(item,index) in row.item.operaItemsText" :key="index">
              {{item}}
              <span class="closeIcon" v-show="row.item.addImgFlag" @click="deleteTag(row,index)">
                <img src="/images/errortips.png" alt="">
              </span>
            </div>
          </div>
          <img class="addImgCon" src="/images/addauthority.png" alt="" @click="openModal(row)" v-show="row.item.addImgFlag">
        </template>
        <template slot="status" slot-scope="row">
          <i-switch size="large" @on-change="changeStatus(row)" :disabled="!row.item.addImgFlag" :value="row.item.status===1?true:false">
            <span slot="open">ON</span>
            <span slot="close">OFF</span>
          </i-switch>
        </template>
        <template slot="rePwd" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="link" @click="rePwd(row)" v-if="row.item.role!=4">
           重置密码
          </b-button>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)" v-show="!row.item.addImgFlag">
           编辑
          </b-button>
          <b-button size="sm" class="mr-2" variant="warning" @click="submit(row)" v-show="row.item.addImgFlag">
           提交
          </b-button>
          <b-button size="sm" class="mr-2" variant="outline-secondary" @click="row.item.addImgFlag=!row.item.addImgFlag" v-show="row.item.addImgFlag">
           取消
          </b-button>
        </template>
      </b-table>
      <b-modal ref="authModal" hide-footer  no-close-on-backdrop title="添加权限">
        <p class="my-4">
          <b-tabs v-model="tabIndex">
            <b-tab title="模块" active>
              <span class="authspan" @click="selectModal(item,index)" v-for="(item,key,index) in modelLists">{{item}}</span>
            </b-tab>
            <b-tab title="权限" :disabled="authTabFlag">
              <span class="authspan" :style="currentModal.selectAuthsText.indexOf(item.desc)>-1?'border-color:#41babc;color:#41babc':''" @click="selectAuth(item.permission,item.desc,item.id)" v-for="(item,index) in authLists" :key="index" >{{item.desc}}</span>
            </b-tab>
          </b-tabs>
          <span class="text-muted">已选中: {{currentModal.selectAuthsText.join('，')}}</span>
          <div>
            <b-button class="mr-3" block  variant="warning" @click="addAuth">提交</b-button>
            <!-- <b-button class="mr-3" block  variant="warning" @click="$refs.authModal.hide()">取消</b-button> -->
          </div>
        </p>
      </b-modal>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>

  export default{
    components:{

    },
    mounted(){
      this.$Loading.start()
      // 设置请求头
      this.$http.defaults.headers.common['Authorization'] = window.$cookies.get("Authorization")||'';
      this.$http.defaults.headers.common['permissionvue'] = window.$cookies.get("permissionvue")||'';
      new Promise((resolve,reject)=>{
        this.$http.post("/api/role/list").then(response=>{
          // console.log(response.data)
          if(!response.data.constructor === Array){
            return false;
          }
          this.rolesLists = response.data
          response.data.forEach(item=>{
            if(this.modelLists.indexOf(item.models)<0){
              this.modelLists.push(item.models)
            }
          })
          resolve()
        })
      }).then(data=>{
        this.getTableData()
      })
            
    },
    data () {
      return {
        operaItems:[],
        operaItemsText:[],
        rolesLists:[],
        modelLists:[],
        authLists:[{desc:''}],
        // currentUser:{},
        currentModal:{
          selectAuths:[],
          selectAuthsText:[],
        },
        tabIndex:0,
        modelText:'',
        authTabFlag:true,
        // selectAuths:[],
        // selectAuthsText:[],
        menus: [{
          text: '权限管理',
          href: '#'
        }],
        roles:[
          {
            value:1,
            text:'外部用户' 
          },
          {
            value:2,
            text:'客服用户' 
          },
          {
            value:3,
            text:'实验用户' 
          },
          {
            value:4,
            text:'管理员' 
          }
        ],
        fields: {
          name:{
            label:'用户名'
          },
          email:{
            label:'邮箱'
          },
          rePwd:{
            label:'重置密码'
          },
          role:{
            label:'角色',
            thStyle:{
              width:"150px"
            }
          },
          permission:{
            label:'权限',
            thStyle:{
              width:"400px",
            },
            tdClass:'authTd'
          },
          status:{
            label:'启用',
            thStyle:{
              width:"150px"
            }
          },
          operate:{
            label:'操作',
            thStyle:{
              textAlign:"center"
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        userLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/role/userlist",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data.data[0])
          response.data.data.forEach(item=>{
            for(let k in item){
              item[k]===null?item[k]='':item[k]=item[k]
            }
            this.$set(item,'addImgFlag',false)
            this.$set(item,'operaItems',[])
            this.$set(item,'operaItemsText',[])
            this.$nextTick(()=>{
              let a = item.permission.split(",")
              a.forEach(itema=>{
                if(itema!==''){
                  item.operaItems.push(itema)
                }
              })
              item.operaItems.forEach(item2=>{
                if(item2!==""){
                  item.operaItemsText.push(this.rolesLists[Number(item2)-1].desc)
                }
              })
            })
          })
          this.userLists = response.data.data
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      rePwd(row){
        this.$Loading.start()
        this.$http.post("/api/permission/resetpwd",qs.stringify({id:row.item.id})).then(response=>{
          this.$Loading.finish()
        }).catch(err=>{
          this.$Loading.error()
        })
      },
      edit(row){
        // 控制页面上一次只能出现一个提交按钮
        row.item.addImgFlag=true
      },
      deleteTag(row,index){
        row.item.operaItems.splice(index,1)
        row.item.operaItemsText.splice(index,1)
        this.$nextTick(()=>{
          row.item.permission=row.item.operaItems.join(',')
        })
        console.log(row.item)
      },
      openModal(row){
        // console.log(row)
        this.$refs.authModal.show()
        // 判断当前是哪个条目被打开，将row赋值给currentModal
        for(let k in row.item){
          this.currentModal[k] = row.item[k]
        }
        this.currentModal.selectAuths=[]
        this.currentModal.selectAuthsText=[]
        this.authTabFlag = false
        this.tabIndex = 0
        // console.log(this.currentModal.permission)
      },
      selectModal(model,index){
        this.modelText=model
        this.authTabFlag = false
        this.authLists = this.rolesLists.filter(item=>{
          return item.models===this.modelText
        })
        this.$nextTick(()=>{
          this.tabIndex = 1
          // console.log(this.modalText)
          // 根据currentModal判断哪些已添加，并隐藏
          let arr = this.currentModal.permission.split(",")
          let arr2=[]
          this.$nextTick(()=>{
            this.authLists.forEach(item=>{
              if(arr.indexOf(String(item.id))<0){
                arr2.push(item)
              }
            })
            this.authLists=arr2
          })
        })
      },
      selectAuth(permission,desc,id){
        let a = this.currentModal.selectAuthsText.filter((item,index) => {
          return item===desc
        })
        if(a.length===0){
          this.currentModal.selectAuths.push(id)
          this.currentModal.selectAuthsText.push(desc)
        }else{
          // 判断是否已选择了该属性,如果已存在则删除该属性
          let i = this.currentModal.selectAuthsText.indexOf(a[0])
          this.currentModal.selectAuths.splice(i,1)
          this.currentModal.selectAuthsText.splice(i,1)
        }
      },
      addAuth(){
        // 点添加即提交
        let obj = {};
        obj.id = this.currentModal.id
        if(this.currentModal.permission===''){
          obj.permission = this.currentModal.selectAuths.join(",")
        }else{
          obj.permission = this.currentModal.permission+ "," +this.currentModal.selectAuths.join(",")
        }
        // console.log(obj)
        // 将添加的权限放到表格中
        this.userLists.forEach(item=>{
          if(item.id===obj.id){
            item.permission=obj.permission
            // 在表格中展示数据
            item.permission = obj.permission
            item.operaItems = []
            item.operaItemsText = []
            item.operaItems.push(...this.currentModal.operaItems,...this.currentModal.selectAuths)
            item.operaItemsText.push(...this.currentModal.operaItemsText,...this.currentModal.selectAuthsText)
            this.$http.post("/api/permission/setpermission",qs.stringify(obj)).then(response=>{
              // console.log(response.data)
              //将模态框状态复原
              this.authTabFlag = true
              this.tabIndex = 0
            })
            return false
          }
        })
        // 关闭模态框
        this.$refs.authModal.hide()
      },

      submit(row){
        // 设置用户角色
        let roleObj={
          id:row.item.id,
          role:row.item.role
        }
        this.$http.post("/api/role/setrole",qs.stringify(roleObj)).then(response=>{
          // console.log(response.data)
        })

        // 设置用户权限
        let permissionObj = {};
        permissionObj.id = row.item.id
        permissionObj.permission = row.item.permission
        this.$http.post("/api/permission/setpermission",qs.stringify(permissionObj)).then(response=>{
          // console.log(response.data)
          //将模态框状态复原
          this.authTabFlag = true
          this.tabIndex = 0
        })
        
        // 设置用户状态
        this.$http.post("/api/role/setaccountswitch",qs.stringify({id:row.item.id,status:row.item.status})).then(response=>{
          // console.log(response.data)
        })
        row.item.addImgFlag=false
      },
      changeStatus (row) {
        // 状态1为启用  2未启用
        if(row.item.status===1){
          row.item.status=2
        }else{
          row.item.status=1
        }
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
  .authBtn{
    margin: 2px 3px;
  }
  /*模态框tab*/
  .tab-pane{
    min-height: 60px;
    padding:8px;
    border-bottom:1px dotted #ccc;
    .authspan{
      display: inline-block;
      color:#aaa;
      padding:3px 10px;
      margin:5px;
      border:1px solid #ccc;
      border-radius: 5px;
      cursor: pointer;
      &:visited{
        color:@themeColor;
        border:1px solid @themeColor;
      }
      &:hover{
        color:@themeColor;
        border:1px solid @themeColor;
      }
    }
  }
  
  .addImg{
    &:hover{
      cursor:pointer;
    }
  }
  .paginationCon{
    overflow: hidden;
    .pagination{
      float: right;
    }
  }
  .ghTagCon{
    max-height:150px;
    overflow-y:auto;
    display: inline-block;
    vertical-align:middle;
    .ghTag{
      font-size:14px;
      box-sizing:border-box;
      display: inline-block;
      padding:2px 8px;
      margin:5px;
      border:1px solid #ccc;
      border-radius: 5px;
      position: relative;
      .closeIcon{
        width: 8px;
        height: 8px;
        position: absolute;
        right:-2px;
        top:-12px;
        img{
          width: 10px;
          height: 10px;
          cursor: pointer;
        }
      }
    }
  }
  .ghTagCon::-webkit-scrollbar {/*滚动条整体样式*/
      width: 3px;     /*高宽分别对应横竖滚动条的尺寸*/
      height: 3px;
  }
  .ghTagCon::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
      border-radius: 5px;
      -webkit-box-shadow: inset 0 0 5px rgba(65,186,188,0.2);
      background: rgba(65,186,188,0.2);
  }
  .ghTagCon::-webkit-scrollbar-track {/*滚动条里面轨道*/
      -webkit-box-shadow: inset 0 0 1px rgba(204,204,204,0.3);
      border-radius: 0;
      background: rgba(204,204,204,0.3);
  }
  .addImgCon{
    vertical-align:middle;
  }
</style>


